          p(numero="3" titulo="<em>Storytelling</em> de valor") En cuanto al #[i storytelling] de valor, transmite un relato de sucesos cotidianos ya sea de los consumidores o de las organizaciones.
          p(numero="4" titulo="<em>Storytelling</em> de consumo") En cuanto al #[i storytelling] de consumo, en el relato o historia, el producto o servicio son los elementos ms relevantes, es decir son los protagonistas de la historia, sin embargo, esta historia no se debe reflejar cmo publicitaria, de lo contrario que el consumidor tenga una experiencia o despierte alguna emocin.
