      h2 3.2 SEO y SEM
    p.mb-4(data-aos="fade-up") El #[i Search Engine Optimization] (SEO) o en el idioma espaol optimizacin para motores de bsqueda y #[i Search Engine Marketing] (SEM) o en el idioma espaol #[i marketing] en motores de bsqueda; son estrategias de #[i marketing] digital, las cuales el canal o medio digital son los buscadores.
    .row.align-items-center.mb-5
