          .col-12.p-4.bg-c12
            p.mb-0 En cuanto el #[i Search Engine Optimization] (SEO), hace parte de las estrategias y tcnicas que emplean las organizaciones, para mejorar la visibilidad de las pginas #[i web], en los motores de bsqueda como Google, Yahoo, Bing, Ask, entre otros.
        p Las estrategias de SEO, se basan en bsquedas orgnicas dentro de los buscadores, es decir que para lograr este posicionamiento no es necesario realizar un pago.
        .row.mb-3
          .col-12.p-4.bg-c6
            .row.align-items-center
